import { useHistory, useLocation } from 'react-router';
import { useQueryParams } from './useQueryParams';
import { useEffect } from 'react';

export function useRedirectUser() {
	const location = useLocation();
	const history = useHistory();
	const { query } = useQueryParams();

	useEffect(() => {
		const redirectingForChangePassword =
			location.pathname.includes('change-password') || location.pathname.includes('resetpassword');

		if (redirectingForChangePassword) {
			history.push(`/change-password?username=${query.username}&code=${query.code}`);
		}
	}, [location.pathname]);
}
